@import url(https://fonts.googleapis.com/css?family=Raleway|Source+Sans+Pro&display=swap);
body{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
}
hr{
  width: 60%;
  max-width: 700px;
  color:white;
}

.form{
  padding: 10vh 1vw;
  font-size: 1.4rem;
  font-family: 'Raleway', sans-serif;
  background-color: rgba(104, 177, 101, 0.815);
  color:white;
  border-radius: 0 0 10px 10px;
}

input{
  margin: 2vh;
  padding: 5px 10px;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid rgb(192, 192, 192);
  font-size: 1rem;
  color: rgb(233, 233, 233);
}

input:focus{
  border-color: white;
  color:white;
}


.form button{
  display: block;
  margin: 3vh auto 0;
  padding: 3px 10px;
  width: 23vw;
  height: 8vh;
  max-width:150px;
  max-height: 70px;
  font-size: 1.4rem;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid white;
  border-radius: 3px;
  color:white;
  -webkit-transition:  .3s;
  transition:  .3s;
  opacity: 0.5;
  cursor: pointer;
}

.form button:hover{
  opacity: 1;
  background-color: #fff;
  color: rgb(103, 177, 101);
  font-size: 1.5rem;
  -webkit-animation: shake 1s linear infinite;
          animation: shake 1s linear infinite;
}
input[type="checkbox"]{
  margin-bottom:0;
}

@-webkit-keyframes shake{
  0%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  10%{
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  20%{
    -webkit-transform: translateX(5%);
            transform: translateX(5%);
  }
  30%{
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  40%{
    -webkit-transform: translateX(5%);
            transform: translateX(5%);
  }
  45%{
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);

  }
}

@keyframes shake{
  0%{
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  10%{
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  20%{
    -webkit-transform: translateX(5%);
            transform: translateX(5%);
  }
  30%{
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  40%{
    -webkit-transform: translateX(5%);
            transform: translateX(5%);
  }
  45%{
    -webkit-transform: translateX(-5%);
            transform: translateX(-5%);
  }
  100%{
    -webkit-transform: translateX(0);
            transform: translateX(0);

  }
}

@media(min-width: 452px){
    .form{
      padding: 10vh 10vw;
    }
}


@media(min-width: 520px){
  .form{
    font-size: 2rem;
    padding: 10vh 5vw;
  }
}
@media(min-width:1024px){
  input[type="checkbox"]{
   display: inline;
  }

  .form, input{
    font-size: 1.8rem;
  }
  .form{
    padding:5vh;
  }
  
}

div.taskToDo, div.taskDone{
  margin: 1vh auto;
  padding: 5px 3vw;
  background-color: rgb(103, 177, 101);
  border-radius: 5px;
  color:white;
  max-width: 750px;
}

div.taskToDo button,
div.taskDone button{
  margin: 0.5vh;
  padding: 5px 12px;
  font-size: 1rem;
  text-transform: uppercase;
  background-color: transparent;
  border: 2px solid white;
  border-radius: 3px;
  color:white;
  -webkit-transition:  .3s;
  transition:  .3s;
  opacity: 0.7;
  cursor: pointer;
}

div.taskToDo button:hover,
div.taskDone button:hover{
  opacity: 1;
  background-color: #fff;
  color: rgb(103, 177, 101);
}

div.taskDone {
  background-color: rgba(104, 177, 101, 0.8);
}

div.taskDone::after{
left: 0
}
div.taskDone button{ 
  font-size: 0.8rem;
}

span.info{
  margin: 5vh;
}
div.active, div.done{
  margin: auto 3vh;
  font-family: 'Raleway', sans-serif;
}
h2, h3{
  font-family: 'Source Sans Pro', sans-serif;
}
div.active{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  font-size: 1.2rem;
}

div.done{
  font-size: 1rem; 
}
body{
  background-image: url(data:image/png;base64,)
}
.App {
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  align-content: center;
  margin: 0 auto;
  border-radius: 5px;
}

div.logo{
  position: relative;
  top:0;
  left:0;
  right:0;
  background-color: rgb(104, 177, 101);
  color:white;
  font-size: 2rem;
  font-family: 'Source Sans Pro', sans-serif;
  
}

div.logo p:nth-child(2){
  font-size: 1.5rem;
}

@media(min-width:1024px){
  div.logo{
    font-size: 3rem;
  }
}


